import { Button, Col, Row } from 'antd';
import React from 'react';
import styled from 'styled-components';

const DivWrapper = styled.div`
	display: block;
	justify-content: center;
	align-items: center;
	padding: 1rem 0rem;
`;
const H1 = styled.h1`
	color: white;
	text-decoration: underline;
	font-size: 11vh;
	display: flex;
	justify-content: Center;
	align-items: center;
	padding: 1rem;
	cursor: pointer;
	font-family: Satisfy;
	transition: all 0.2s ease-in-out;
	&:hover {
		color: #ffd700;
	}
`;

const StyledButton = styled(Button)`
  color: white;
  height: 5rem;
  background-color: #010101;
  border: 1px solid white;
  &:hover {
    color: #010101;
    border: 1px solid white;
  }
`;

const COL = styled(Col)`
display:flex;
justify-content:center;
align-items:center;
@media (max-width:600px){
	display:block
}
`;

const Landing = () => {
	const cv = 'https://drive.google.com/file/d/1fy936g7LVyvhGOr3OyxQkk5M3vJLTOzJ/view?usp=sharing';
	return (
		<DivWrapper>
			<Row>
				<Col span={24}>
					<H1>Sanjay in 10 seconds.</H1>
				</Col>
				<Col span={24} style={{ color: 'white' }}>
					<div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
						<h2
							style={{
								textAlign: 'center',
								color: 'white',
								maxHeight: 500,
								width: '500px',
							}}
						>
							My name is Sanjay Pun and I'm a Web Developer who specializes in ReactJS. I am also a
							traveller, a cook, a good listener. Monomaniac, introvert, good learner, and love finding a
							different point of view. Baglung native, I now live in Lalitpur, Nepal.
						</h2>
					</div>
				</Col>
			</Row>
			<Row>
				<COL span={24}>
					<StyledButton style={{ margin: '1rem' }}>
						<a style={{ fontSize: '3rem' }} href='mailto:iamsanjaypun@gmail.com'>
							Hire Me!
						</a>
					</StyledButton>
					<StyledButton
						style={{ height: '5rem', margin: '1rem' }}
						xs={24}
						sm={24}
						md={24}
						lg={12}
						xl={12}
						onClick={() => window.open(cv, '_blank')}
					>
						<p style={{ fontSize: '3rem', padding: '0rem 0.7rem 0rem 0.7rem' }}>See CV</p>
					</StyledButton>
				</COL>
			</Row>
		</DivWrapper>
	);
};
export default Landing;
